
"use client";

import { useState, useEffect, useCallback } from "react";
import { auth, db } from "@/lib/firebase";
import { User, onAuthStateChanged, signInAnonymously } from "firebase/auth";
import { doc, getDoc, setDoc } from "firebase/firestore";
import type { AppData } from "@/lib/types";
import { isSameDay, subDays } from "date-fns";

const initialData: AppData = {
  streak: 0,
  lastLoginDate: new Date().toISOString(),
  studyLog: {},
  journalEntries: [],
  completedTasks: {},
  rewards: [],
  galleryPhotos: [],
};

export function useFirestoreData() {
  const [data, setDataState] = useState<AppData | null>(null);
  const [user, setUser] = useState<User | null>(null);
  const [loading, setLoading] = useState(true);

  // Effect for handling authentication
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (currentUser) => {
      if (currentUser) {
        setUser(currentUser);
      } else {
        // No user, sign in anonymously. The listener will catch the new user state.
        try {
          await signInAnonymously(auth);
        } catch (error) {
          console.error("Anonymous sign-in failed:", error);
          // If sign-in fails, we can't proceed.
          setLoading(false);
        }
      }
    });

    return () => unsubscribe();
  }, []); // Runs only once on mount

  // Effect for fetching/creating data once the user is available
  useEffect(() => {
    if (!user) {
      // Don't do anything if there's no user yet
      return;
    }

    // Hydrate from localStorage immediately for faster paint
    const localKey = `appData:${user.uid}`;
    try {
      const cached = typeof window !== "undefined" ? window.localStorage.getItem(localKey) : null;
      if (cached) {
        const parsed: AppData = JSON.parse(cached);
        setDataState(parsed);
      }
    } catch {}

    const fetchData = async () => {
      const docRef = doc(db, "users", user.uid);
      try {
        const docSnap = await getDoc(docRef);
        if (docSnap.exists()) {
          const firestoreData = docSnap.data() as AppData;
          const today = new Date();
          const lastLogin = new Date(firestoreData.lastLoginDate);

          // Update streak logic
          if (!isSameDay(today, lastLogin)) {
            const yesterday = subDays(today, 1);
            const newStreak = isSameDay(yesterday, lastLogin) ? (firestoreData.streak || 0) + 1 : 1;
            const updatedData = { ...firestoreData, streak: newStreak, lastLoginDate: today.toISOString() };
            // Update db without waiting for it to complete for faster UI response
            setDoc(docRef, updatedData, { merge: true });
            setDataState(updatedData);
            try {
              window.localStorage.setItem(localKey, JSON.stringify(updatedData));
            } catch {}
          } else {
            setDataState(firestoreData);
            try {
              window.localStorage.setItem(localKey, JSON.stringify(firestoreData));
            } catch {}
          }
        } else {
          // New user, create initial data
          const newUserData = { ...initialData, streak: 1, lastLoginDate: new Date().toISOString() };
          await setDoc(docRef, newUserData);
          setDataState(newUserData);
          try {
            window.localStorage.setItem(localKey, JSON.stringify(newUserData));
          } catch {}
        }
      } catch (error) {
        console.error("Error fetching or creating user data:", error);
        // Set initial data locally on error to allow app to function
        setDataState(initialData);
        try {
          window.localStorage.setItem(localKey, JSON.stringify(initialData));
        } catch {}
      } finally {
        setLoading(false);
      }
    };
    
    fetchData();
  }, [user]); // This effect runs whenever the user object changes

  const setData = useCallback(
    (updater: (prevData: AppData | null) => AppData | null) => {
      if (!user) {
        console.error("Cannot set data: no user is authenticated.");
        return;
      }
      
      setDataState(prevData => {
        const newData = updater(prevData);
        if (newData) {
          const docRef = doc(db, "users", user.uid);
          // Use { merge: true } to only update fields that have changed.
          setDoc(docRef, newData, { merge: true }).catch(error => {
            console.error("Error writing to Firestore:", error);
          });
          // Update cache for snappy subsequent renders
          try {
            window.localStorage.setItem(`appData:${user.uid}`, JSON.stringify(newData));
          } catch {}
        }
        return newData;
      });
    },
    [user] // Dependency on user ensures the correct UID is used
  );

  return { data, setData, loading, user };
}
