// Import the functions you need from the SDKs you need
import { initializeApp } from "firebase/app";
import { getAuth } from "firebase/auth";
import { getFirestore, enableIndexedDbPersistence } from "firebase/firestore";
import { getStorage, ref, uploadBytes, getDownloadURL } from "firebase/storage";

// Your web app's Firebase configuration
const firebaseConfig = {
  "projectId": "studybuddy-homeschool",
  "appId": "1:34728672705:web:d1e876cfd0031cba448340",
  "storageBucket": "studybuddy-homeschool.appspot.com",
  "apiKey": "AIzaSyAXAZ1aX8HD7FR2ql2fqeHXgBgSq_btErk",
  "authDomain": "studybuddy-homeschool.firebaseapp.com",
  "measurementId": "",
  "messagingSenderId": "34728672705"
};

// Initialize Firebase
const app = initializeApp(firebaseConfig);
export const auth = getAuth(app);
export const db = getFirestore(app);
export const storage = getStorage(app);

// Enable offline persistence to speed up reads after first load
if (typeof window !== "undefined") {
  enableIndexedDbPersistence(db).catch(() => {
    // Ignore persistence errors (e.g., private mode or multiple tabs)
  });
}

// --- Firebase Storage Helper ---
export const uploadPhoto = async (userId: string, file: File): Promise<string> => {
    const filePath = `users/${userId}/gallery/${Date.now()}-${file.name}`;
    const storageRef = ref(storage, filePath);
    const snapshot = await uploadBytes(storageRef, file);
    const downloadUrl = await getDownloadURL(snapshot.ref);
    return downloadUrl;
};
